<template>
  <div class="book-like">
    <h1>Liked Book</h1>
    <hr />
    <small class="mt-3">if the book that you unlisted is not lost please refresh the page</small>
    <div class="text-center w-100" v-if="getLikedBook.length == 0">
      <img class="empty-img mt-5" :src="require('@/assets/img/empty.jpg')" alt />
      <h1 class="text-center mt-5">Your don't have Liked Book</h1>
    </div>
    <div v-else class="all-book mt-5">
      <CardBook v-for="(book,index) in getLikedBook" :key="index" :book="book" />
    </div>
  </div>
</template>

<script>
import { mapState, mapGetters } from "vuex";
export default {
  name: "bookLike",
  components: {
    CardBook: () => import("../CardBook"),
  },
  data() {
    return {
      likedBook: [],
    };
  },
  watch: {
    getLikedBook(val) {
      return val;
    },
  },
  computed: {
    ...mapState(["user"]),
    ...mapGetters(["getLikedBook"]),
  },
};
</script>

<style lang="scss" scoped>
.book-like {
  padding: 4rem;
  min-height: 400px;

  .all-book {
    display: grid;
    min-height: 20vh;
    width: 100%;
    grid-template-columns: repeat(auto-fill, minmax(220px, 1fr));
    grid-template-rows: 1fr;
    grid-gap: 3rem;
  }

  .empty-img {
    width: 300px;
    height: 300px;
  }
}
</style>